import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import Moment from "react-moment";
import "moment/locale/es";
import Global from "../Global";
import ImgDef from "../assets/images/default.png";

class Articles extends Component {
  url = Global.url;

  state = {
    articles: [],
    status: null,
  };

  componentDidMount() {
    var home = this.props.home;
    var search = this.props.search;
    if (home === "true") {
      this.getLastArticles();
    } else if (search && search != null && search !== undefined) {
      this.getSearchArticles(search);
    } else {
      this.getArticles();
    }
  }

  getSearchArticles = (searched) => {
    axios
      .get(this.url + "search/" + searched)
      .then((res) => {
        this.setState({
          articles: res.data.articles,
          status: res.data.status,
        });
      })
      .catch((err) => {
        this.setState({
          articles: [],
          status: "success",
        });
      });
  };

  getLastArticles = () => {
    axios.get(this.url + "articles/last").then((res) => {
      this.setState({
        articles: res.data.articles,
        status: res.data.status,
      });
    });
  };

  getArticles = () => {
    axios.get(this.url + "articles").then((res) => {
      this.setState({
        articles: res.data.articles,
        status: res.data.status,
      });
    });
  };

  render() {
    if (this.state.articles.length >= 1) {
      var listArticles = this.state.articles.map((article) => {
        return (
          <article className="article-item" key={article._id}>
            <div className="image-wrap">
              {article.image !== null ? (
                <img
                  src={this.url + "get-image/" + article.image}
                  alt={article.title}
                />
              ) : (
                <img src={ImgDef} alt="Paisaje" />
              )}
            </div>
            <h2>{article.title}</h2>
            <span className="date">
              <Moment fromNow>{article.date}</Moment>
            </span>
            <Link to={"blog/articles/" + article._id}>Leer más</Link>
            <div className="clearfix" />
          </article>
        );
      });

      return <div id="articles">{listArticles}</div>;
    } else if (
      this.state.articles.length === 0 &&
      this.state.status === "success"
    ) {
      return (
        <div id="articles">
          <h2 className="subheader">No hay artículos para mostrar</h2>
          <p>Aún no hay contenido en esta sección</p>
        </div>
      );
    } else {
      return (
        <div id="articles">
          <h2 className="subheader">Cargando...</h2>
          <p>Espere un momento en lo que obtenemos los datos necesarios....</p>
        </div>
      );
    }
  }
}
export default Articles;
